import { call, put, select } from 'redux-saga/effects'
import { Creators } from '../actions'
import { loggedToken } from '../redux/LoginRedux'

const userToken = ({ login }) => loggedToken(login)

export function * getStoreHistory(api, { storeId }) {
  const token = yield select(userToken)
  api.setHeaders({'Authorization': `Bearer ${token}`})

  const response = yield call(api.getStoreHistory, { storeId })
  const { ok, data } = response

  if (ok) {
    const { history } = data

    yield put(Creators.storeHistorySuccess(history))
  } else {
    const { error } = data

    yield put(Creators.storeHistoryFailure(error))
  }
}
